<template>
    <div class="navbar-warp">
        <b-navbar toggleable="lg" type="light" variant="light">
            <b-navbar-brand href="#" @click="$router.push({name:'Index'})">
                <img class="header-logo" src="../assets/logo.png">
                <span class="text-color" style="color:#1296db;padding-left:8px;">MicroGalaxy</span>
            </b-navbar-brand>

            <b-navbar-toggle target="nav_collapse"/>

            <b-collapse is-nav id="nav_collapse">
                <b-navbar-nav>

                    <b-nav-item v-for="(item,index) in navTexts" href="#">
                        <span :class="[item.active ? 'text-color' : 'text-black']"
                              @click="clickNav(index)">
                            {{item.name}}</span>
                        <div :class="[item.active ? 'nav-text-bottom' : '']"></div>
                    </b-nav-item>
                </b-navbar-nav>

                <!-- Right aligned nav items -->
                <b-navbar-nav class="ml-auto">
                    <b-nav-form>
                        <input
                                type="text"
                                class="search-input"
                                v-model="searchString"
                                placeholder="Article search"
                                style="font-weight: 500"
                        >
                        <!-- <b-form-input size="sm" class="mr-sm-2" type="text" placeholder="Article search"/> -->
                        <!-- <b-button size="sm" class="my-2 my-sm-0" type="submit">Search</b-button> -->
                        <img src="../assets/navigate/searchButton.png" class="search-button-img">
                    </b-nav-form>

                    <b-nav-item href="#">
                        <div class="cu-tag bg-gradual-blue shadow sm light round" style="left: -25px;"
                             @click="writeArticle()">Write article
                        </div>
                        <img src="../assets/navigate/writeArticle.png" class="write-article-img">
                    </b-nav-item>

                    <!-- <b-nav-item-dropdown text="Lang" right>
                                <b-dropdown-item href="#">EN</b-dropdown-item>
                                <b-dropdown-item href="#">ES</b-dropdown-item>
                                <b-dropdown-item href="#">RU</b-dropdown-item>
                                <b-dropdown-item href="#">FA</b-dropdown-item>
                    </b-nav-item-dropdown>-->
                    <b-nav-item-dropdown right>
                        <!-- Using button-content slot -->
                        <template slot="button-content">
                            <em class="text-color">User</em>
                        </template>
                        <b-dropdown-item href="#">Sign in</b-dropdown-item>
                        <b-dropdown-item href="#">Sign up</b-dropdown-item>

                        <b-dropdown-item href="#">Signout</b-dropdown-item>

                        <b-dropdown-item href="#">About</b-dropdown-item>
                    </b-nav-item-dropdown>
                </b-navbar-nav>
            </b-collapse>
        </b-navbar>
    </div>
</template>


<script>
    import {checkToken} from "@/utils"

    export default {
        data() {
            return {
                searchString: "",
                navTexts: [{
                    name: 'Home',
                    routerName: 'Index',
                    active: true
                },
                //     {
                //     name: 'Life',
                //     routerName: 'life',
                //     active: false
                // }, {
                //     name: 'Explore',
                //     routerName: 'explore',
                //     active: false
                // }, {
                //     name: 'Tools',
                //     routerName: 'tools',
                //     active: false
                // }
                ]
            };
        },

        methods: {
            clickNav(optionIndex) {
                let indexActive = 0
                for (let index in this.navTexts) {
                    optionIndex == index ? (this.navTexts[index].active = true, indexActive = optionIndex) : this.navTexts[index].active = false
                }
                this.$router.push({name: this.navTexts[indexActive].routerName})
            },
            writeArticle() {
                if (checkToken()) {
                    this.$router.push({name: 'WriteArticle'})
                } else {
                    this.$router.push({name: 'Login'})
                }
            }
        }
    };
</script>

<style scoped>
    .header-logo {
        width: 36px;
    }

    .nav-text-bottom {
        height: 3px;
        width: 100%;
        border-radius: 5000px;
        margin-top: 3px;
        background-image: linear-gradient(45deg, #0081ff, #1cbbb4)
    }

    .search-input:focus {
        border: 0px;
        width: 280px;
        transition: 0.5s;
    }

    .search-input {
        outline: none;
        padding: 0 40px 0 20px;
        width: 220px;
        height: 32px;
        font-size: 14px;
        border: 1px solid #eee;
        border-radius: 40px;
        background: #eee;
        transition: width 0.5s;
        -moz-transition: width 0.5s;
        -webkit-transition: width 0.5s;
        -o-transition: width 0.5s;
        transition-delay: 0.1s;
        -moz-transition-delay: 0.1s;
        -webkit-transition-delay: 0.1s;
        -o-transition-delay: 0.1s;
    }

    .search-button-img {
        position: relative;
        cursor: pointer;
        /*top: 4px;*/
        float: right !important;
        z-index: 1;
        right: 28px;
        width: 18px;
        height: 18px !important;
        line-height: normal !important;
        padding: 0 !important;
        color: #969696 !important;
        text-align: center;
    }

    .write-article-img {
        position: relative;
        cursor: pointer;
        /*top: 4px;*/
        float: left !important;
        z-index: 3;
        top: -10px;
        left: 95px;
        width: 25px;
        height: 25px !important;
        line-height: normal !important;
        padding: 0 !important;
        color: #969696 !important;
        text-align: center;
    }

    .navbar-warp {
        opacity: 0.85;
    }
</style>
